export default {
	data() {
		return {
			loginPhone: 'kehu',
			loginPassword: 'anxuan14888',
		}
	},
	onShow() {

	},
	mounted() {

	},
	methods: {
		Logo: function() {
			if (this.loginPhone == '') {
				uni.showToast({
					title: "请输入账号", icon: "none"
				}) 
				return false
			}
			if (this.loginPassword == '') {
				uni.showToast({
					title: "请输入密码", icon: "none"
				}) 
				return false
			}
			uni.showLoading({title: '正在登陆'}); 
			var obj = {
				url: this.$store.state.url + 'Account/Login',
				method: 'POST',
				data: {
					username: this.loginPhone,
					password: this.loginPassword
				}
			}
			this.$http(obj).then((res) => {
				setTimeout(function () {uni.hideLoading();}, 100);  //清除过渡
				this.$store.state.CustomerId = JSON.parse(res.Data).CusId
				this.$store.state.username = this.loginPhone
				this.$store.state.token = res.Data //数据中心在线存储
				uni.setStorage({ //将token存储在本地
					key: 'Token',
					data: res.Data,
					success: function() {
						uni.setStorage({ //将用户名存储在本地
							key: 'admin',
							data: this.loginPhone,
							success: function() {
								uni.switchTab({ //跳转至主页
									url: '/pages/home/home'
								})
							}
						});
					}
				});
			})
		}

	}
}
